import logo from './logo.svg';
import React, { Component } from 'react'
import './App.css';
import Home from './cmps/home'
import { connect } from 'react-redux'
import actions from './actions/mainaction'
import LoadingOverlay from 'react-loading-overlay';
import { PopupActions, DialogType, AnimationType } from "react-custom-popup";
import axios from 'axios';
var crsinterval = ''
class APP extends Component {

  componentDidMount() {
    crsinterval = setInterval(() => {
      this.gwtCsrf()
    }, 1000 * 60);
    this.gwtCsrf()
  }


  getlog = (tkn) => {
    axios.post(`http://localhost:9990/login/`, {}, {
      headers: {
        'x-csrf-token': tkn
      }
    })
      .then(res => {
        console.log(" csrf log ", res)
        this.props.setSummary('crftokn', res.data.csrfToken)
      }).catch(res => {
        console.log('csrfcathc log:', res)
      })
  }

  gwtCsrf = () => {
    axios.get(`http://localhost:9990/csrf/`)
      .then(res => {
        console.log(" csrf", res)
        this.props.setSummary('crftokn', res.data.csrfToken)
        // axios.defaults.headers.common['X-CSRF-TOKEN'] = res.data.csrfToken
        this.getlog(res.data.csrfToken)
      }).catch(res => {
        console.log('csrfcathc:', res)
      })
  }

  render() {
    const { main } = this.props
    console.log('props:', this.props)
    const { summary } = main
    const { showLoading } = summary
    return (
      <div className="App">
        <LoadingOverlay
          active={showLoading}
          spinner
          text='Loading your content...'
        >
          <Home />
        </LoadingOverlay>
      </div>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  setSummary: (key, value) => dispatch(actions.setSummary(key, value)),
})
const mapStateToProps = state => ({
  main: state.Main,
})


// export default withRouter(Backup)
export default connect(mapStateToProps, mapDispatchToProps)(APP)
